.app__works-header {
    padding: 6rem 5rem 0 5rem;
    max-width: 1000px;
    margin: auto;
    color: var(--secondary-color);

    @media screen and (max-width: 800px ) {
        text-align: center;
    }

    @media screen and (max-width: 667px) {
        padding: 3rem 5rem 0rem 5rem;
    }
}

.app__works-filter {
    display: flex;
    flex-direction: row;
    // justify-content: flex-start;
    align-items:center;
    flex-wrap: wrap;

    margin: 5rem 0 2rem;

    .app__work-filter-item {
        padding: 0.5rem 1rem;
        border-radius: 0.5rem;
        background-color: var(--border);
        -webkit-backdrop-filter: blur(10px);
        /* Safari 9+ */
        backdrop-filter: blur(10px);
        /* Chrome and Opera */
        -webkit-box-shadow: inset 0 0 0 800px rgba(67, 79, 94, 0.1);
                box-shadow: inset 0 0 0 800px rgba(67, 79, 94, 0.1);
        
        font-weight: 600;
        cursor:pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;

        &:hover{
            transform: scale(1.1);
            background-color: var(--secondary-color);
        }

        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
            border-radius: 0.85rem;
        }
    }
    .item-active {
        background-color: var(--secondary-color);
    }

    @media screen and (max-width: 667px) {
        margin: 2rem 0 0 0 ;
    }
}

.app__works-portfolio {

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 2rem 0 3rem 0;

    .app__work-item {
        width: 270px;
        flex-direction: column;

        margin: 1rem;
        padding: 1rem 1rem 0.2rem 1rem;
        border-radius: 0.5rem;
        background-color: var(--border);
        // background-color: var(--secondary-color);
        -webkit-backdrop-filter: blur(10px);
        /* Safari 9+ */
        backdrop-filter: blur(10px);
        /* Chrome and Opera */
        -webkit-box-shadow: inset 0 0 0 800px rgba(67, 79, 94, 0.1);
                box-shadow: inset 0 0 0 800px rgba(67, 79, 94, 0.1);

        cursor: pointer;
        transition: all 0.3s ease;

        &:hover{
            box-shadow: 0 0 25px var(--secondary-color);
            transform: scale(1.1);
        }

        @media screen and (min-width: 2000px) {
            width: 470px;
            padding: 1.25rem;
            border-radius: 0.75rem;
        }

        @media screen and (max-width: 400px) {
            width: 75%;
            margin: 1rem;
            padding:1rem 1rem 0.2rem 1rem;
        }

        .app__work-img {
            width: 100%;
            height: 230px;

            position: relative;

            p {
                position:absolute;
                width: 80%;
                text-align: justify;
                font-weight: 600;
                color: var(--white);
            }

            img {
                width: 100%;
                height: 100%;
                border-radius: 0.5rem;
                object-fit: cover;
            }

            @media screen and (min-width: 2000px) {
                height: 350px;
            }
        }
    }
    @media screen and (max-width: 667px) {
        padding: 1rem 0 3rem;
    }

    @media screen and (min-width: 800px) {
        padding: 2rem 0 5rem;
    }
}

.app__work-hover {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.7);

    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.3s ease;

    div {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0,0,0,0.5);
        color: var(--white);

        margin: 1rem;
        font-family: var(--font-base);
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;

        svg {
            width: 50%;
            height: 50%;
            color: var(--white);
        }

    }
}

.app__work-content {
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;

    h4 {
        font-size: 1.2rem;
        margin: 0;
        line-height: 1.5;
        padding-top:0.5rem;

        @media screen and (max-width: 400px) {
            padding-top:1rem;
        }
    }

    .app__work-tag {
        position: absolute;
        padding: 0 1rem;
        border-radius: 10px;
        background-color: var(--border-solid);
        color: var(--white);
        font-size: 0.8rem;
        margin: 0;
        top: -30px;
    }
}




